@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}
#hoverLogo:hover {
    content: url(/static/media/logo_coral_drop_shadow.0bedd16d.png);
}
.nav {
  overflow: hidden;
  /* background-color: #00acb0; */
}

.nav-link {
  float: left;
  /* color: #f2f2f2; */
  /* background-color: #00acb0; */
  text-align: center;
  /* padding: 24px 10px; */
  text-decoration: none;
  font-size: 15px;
  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: #00818300;
}

.nav-right {
  float: right;
}
footer {
    margin-top: 80px;
}

/* Font: Source Sans Pro  */

body {
    font-family: 'Source Sans Pro', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
    background-color: rgba(245, 245, 245, 0.539);
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #00acb0;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}


.DemoBackground{
    background: #d8d8d8; 
